import * as React from "react";
import View from "react-flexview";
import { Popup } from "./Popup";
import { OpenStreetMapNode } from "./getOpenStreetMapAmenities";
import localforage from "localforage";
import { osmAuth } from "./osmAuth";

type Option<V extends string> = {
  value: V;
  label: string;
};

const Select = <V extends string>(props: {
  value?: V;
  label: string;
  onChange: (value: V) => void;
  options: Option<V>[];
}) => {
  return (
    <View column className="select" shrink={false}>
      <span className="select-label">{props.label}</span>
      <select
        value={props.value}
        onChange={e => props.onChange(e.currentTarget.value as V)}
      >
        {props.options.map(o => (
          <option key={o.value} value={o.value}>
            {o.label}
          </option>
        ))}
      </select>
    </View>
  );
};

const Input = (props: {
  value?: string;
  label: string;
  onChange: (value: string) => void;
}) => {
  return (
    <View column className="input" shrink={false}>
      <span className="input-label">{props.label}</span>
      <input
        value={props.value}
        onChange={e => props.onChange(e.currentTarget.value)}
      />
    </View>
  );
};

export const UpsertNodePopup = (props: {
  node: Omit<OpenStreetMapNode, "id"> & { id: string | null };
  onClose: () => void;
}) => {
  const [node, updateNode] = React.useState({ ...props.node });

  const [state, updateState] = React.useState<
    | { status: "Unknown" }
    | {
        status: "LoggedIn";
        token: string;
        expiresAt: number;
        refreshToken: string;
      }
    | { status: "LoggedOut" }
  >({ status: "Unknown" });

  React.useEffect(() => {
    localforage
      .getItem<{ token: string; expiresAt: number; refreshToken: string }>(
        "tokens"
      )
      .then(tokens => {
        if (tokens && Date.now() < tokens.expiresAt) {
          updateState({
            status: "LoggedIn",
            ...tokens
          });
        }

        updateState({ status: "LoggedOut" });
      });
  }, []);

  const editNodeTag = (tag: string, value: string) => {
    updateNode({
      ...node,
      tags: {
        ...node.tags,
        [tag]: value
      }
    });
  };

  const getForm = () => {
    return (
      <View column shrink={false}>
        {(node.tags.amenity === "toilets" ||
          node.tags.amenity === "shower") && (
          <Select
            value={node.tags.access}
            label="Access"
            onChange={v => editNodeTag("access", v)}
            options={[
              { value: "unknown", label: "Unknown" },
              { value: "yes", label: "Yes" },
              { value: "public", label: "Public" },
              { value: "permissive", label: "Permissive" }
            ]}
          />
        )}

        {(node.tags.amenity === "toilets" ||
          node.tags.amenity === "shower") && (
          <Select
            value={node.tags.fee}
            label="Fee"
            onChange={v => editNodeTag("fee", v)}
            options={[
              { value: "unknown", label: "Unknown" },
              { value: "yes", label: "Yes" },
              { value: "no", label: "No" }
            ]}
          />
        )}

        {(node.tags.amenity === "toilets" || node.tags.amenity === "shower") &&
        node.tags.fee === "yes" ? (
          <Input
            value={node.tags.charge}
            label="Fee Amount"
            onChange={v => editNodeTag("charge", v)}
          />
        ) : (
          <div />
        )}

        {node.tags.amenity === "toilets" && (
          <Select
            value={node.tags.unisex}
            label="Gender"
            onChange={v => editNodeTag("unisex", v)}
            options={[
              { value: "yes", label: "Yes" },
              { value: "male", label: "Male" },
              { value: "female", label: "Female" }
            ]}
          />
        )}

        {node.tags.amenity === "toilets" && (
          <Select
            value={node.tags.changing_table}
            label="Changing Table"
            onChange={v => editNodeTag("changing_table", v)}
            options={[
              { value: "unknown", label: "Unknown" },
              { value: "yes", label: "Yes" },
              { value: "no", label: "No" }
            ]}
          />
        )}

        {node.tags.amenity === "shower" && (
          <Select
            value={node.tags.hot_water}
            label="Hot Water"
            onChange={v => editNodeTag("hot_water", v)}
            options={[
              { value: "unknown", label: "Unknown" },
              { value: "yes", label: "Yes" },
              { value: "no", label: "No" }
            ]}
          />
        )}

        {(node.tags.amenity === "toilets" ||
          node.tags.amenity === "shower") && (
          <Select
            value={node.tags.wheelchair}
            label="Wheelchair"
            onChange={v => editNodeTag("wheelchair", v)}
            options={[
              { value: "unknown", label: "Unknown" },
              { value: "yes", label: "Yes" },
              { value: "no", label: "No" },
              { value: "limited", label: "Limited" }
            ]}
          />
        )}
      </View>
    );
  };

  const getTitle = (): string => {
    switch (props.node.tags.amenity) {
      case "drinking_water":
        return "Drinking Water";
      case "toilets":
        return "Toilets";
      case "shower":
        return "Shower";
    }
  };

  if (false) {
    return (
      <Popup onClose={props.onClose} isOpen={true}>
        <h2>{getTitle()}</h2>

        {getForm()}

        <button
          style={{
            marginTop: 48,
            height: 40,
            width: 200,
            marginLeft: "auto",
            flexShrink: 0
          }}
        >
          Save on OSM
        </button>
      </Popup>
    );
  } else {
    return (
      <Popup onClose={props.onClose} isOpen={true}>
        <h2>
          You need to be logged into Open Street Maps to add and edit nodes
        </h2>

        <button
          style={{
            marginTop: 48,
            height: 40,
            width: 200,
            marginLeft: "auto",
            flexShrink: 0
          }}
          onClick={() => {
            location.href = osmAuth.code.getUri();
          }}
        >
          Log in OSM
        </button>
      </Popup>
    );
  }
};
